/**
 * CSS selector client side filter.
 *
 * @module     tool_usertours/filter_cssselector
 * @class      filter_cssselector
 * @package    tool_usertours
 * @copyright 2020 The Open University
 * @license http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */

/**
 * Checks whether the configured CSS selector exists on this page.
 *
 * @param {Object} tourDetail The tour configuration to be checked
 * @returns {Bool}
 */
export const filterMatches = tourDetail => {
    if (tourDetail.filtervalues.cssselector && tourDetail.filtervalues.cssselector.length) {
        return !!document.querySelector(tourDetail.filtervalues.cssselector[0]);
    }

    // If there is no CSS selector configured, this page matches.
    return true;
};
